@import "../../sass/variables";
@import "../../sass/mixin";
.header {
    text-align: center;
    margin: 1rem;
    padding: 0.5rem;
    margin-top: 10rem;
    .text-shadow {
        color: $secondaryColor;
        font-size: 3rem;
        text-transform: uppercase;
        text-decoration: underline;
        letter-spacing: .3rem;
        animation: flicker 0.5s ease-in-out infinite alternate;
        transition: all 0.5s ease-in-out;
        &:hover {
            color: $cardNavBgColor;
            cursor: pointer;
        }
    }
    @keyframes flicker {
        0%{
            opacity: .5;
            text-shadow: 2px 2px 10px $cardNavBgColor;
        }
        100%{
            opacity: 1;
            text-shadow: 2px 2px 10px $cardNavBgColor;
        }
        
    }
}
@include media-xsm {
    .header {
        margin-top: 20rem;
    }
}
@include media-lg {
    .header {
        margin-top: 10rem;
    }
}